                        <img src="@/assets/IMG/common/star.svg" alt="   " class="g-star r-star"/>
	                    </li>
	                    <li id="star_2" class="popup-star-li" @click="set_review_star($event.target)">
                        <img src="@/assets/IMG/common/y-star.svg" alt="   " class="y-star r-star blind"/>
                        <img src="@/assets/IMG/common/star.svg" alt="   " class="g-star r-star"/>
	                    </li>
